import React, { Component } from 'react'

export class WeaponSkill extends Component {
    render() {
        return (
            <tr>
                <td> {this.props.name} </td>
                <td> : {this.props.count}</td>
                <td>                 
                    <button style={{width:"24px"}} onClick={this.props.click.bind(this, this.props.index)}>+</button>
                    <button style={{width:"24px"}} onClick={this.props.minusClick.bind(this, this.props.index)}>-</button>
                </td>
            </tr>
        )
    }
}

export default WeaponSkill
