import React, { Component } from 'react'

export class Summons extends Component {
    render() {
        return (
            <div>
                <input type="radio" name="summons" value={3.8}  onChange={this.props.click}/> 2 Primals/ULB Manga<br/>
                <input type="radio" name="summons" value={2.4} onChange={this.props.click}/> 1 Primal/ULB Magna<br/>
                <input type="radio" name="summons" value={1} onChange={this.props.click}/> 0 Primal<br/>  
                <input type="radio" name="summons" value={3.7} onChange={this.props.click}/> 1 Demi + 1 Primal<br/>
                <input type="radio" name="summons" value={2.3} onChange={this.props.click}/> 1 Demi<br/>
                <input type="radio" name="summons" value={3.4} onChange={this.props.click}/> 2 FLB magna<br/>
                <input type="radio" name="summons" value={3.6} onChange={this.props.click}/> 1 FLB/1 ULB magna<br/>
                <input type="radio" name="summons" value={2.2} onChange={this.props.click}/> 1 magna FLB<br/>

            </div>
        )
    }
}

export default Summons
